    .article2 {
        .row {
            ul {
                display: flex;
                flex-wrap: wrap; // 줄바꿈
                padding: 30px 0;
                li {
                    width: 33.33%;
                    padding: 20px;
                    a {
                        display: flex;
                        img {
                            width: 40%;
                            margin-right: 10px;
                            border-radius: 10px;
                        }
                        div {
                            align-self: center; // 부모높이의 중간으로 세로정렬
                        }
                    }
                }
            }
            .lifeStyle {

                h2 {
                    font-size: 20px;                    
                }
                ul {
                    display: flex;
                    li {
                        width: 25%;
                        padding: 0 10px;
                        img {
                            width: 100%;
                            border-radius: 10px;
                        }
                    }
                }
            }
        }
    }
    .article3 {
        background: #000;
        color: #fff;
        padding: 100px 0;
        .row {
            display: flex;
            div {
                width: 52%;
                img {
                    width: 100%;
                    padding: 10px 10px 0;
                    border-radius: 30px 30px 0 0;
                    vertical-align: middle; // 사진사이에 빈틈이 생기는것 해결
                }
                p {
                    background: #222222;
                    margin: 0 10px;
                    padding: 20px;
                    border-radius: 0 0 30px 30px;
                    font-size: 14px;
                }
                &.right {
                    width: 48%;
                    display: flex;
                    flex-direction: column; // 상하(세로)로 정렬
                    justify-content: space-between; // 양쪽 끝으로 붙게 (상하or좌우 끝으로)
                    div {
                        width: 100%;
                    }
                    .rightFst {
                        display: flex;
                        flex-direction: row;
                    }
                }
            }
        }
    }
}