@import url(https://fonts.googleapis.com/css2?family=Poor+Story&family=Roboto+Mono&family=Ubuntu&display=swap);
.headerWrap{position:fixed;top:0;left:0;width:100%;color:#fff;transition:all 0.5s;z-index:9}.headerWrap .row{display:flex;height:80px;justify-content:space-between;position:relative;align-items:center}.headerWrap .row .nav{position:absolute;left:50%;transform:translate(-50%)}.headerWrap .row .nav .depth1{display:flex}.headerWrap .row .nav .depth1 li{position:relative;padding:20px 0;cursor:pointer;margin:0 30px}.headerWrap .row .nav .depth2{position:absolute;top:100%;left:50%;width:150px;background:#fff;transform:translate(-50%);padding:20px 30px;color:#000;border-radius:10px;display:none}.headerWrap .row .nav .depth2.on{display:block}.headerWrap .row .nav .depth2 li{padding:10px 0;margin:0}.headerWrap .row .mobileNav{position:relative}.headerWrap .row .mobileNav button{padding:10px 20px;border-radius:50px;border:none;font-size:20px;display:flex;cursor:pointer}.headerWrap .row .mobileNav button .ham{margin-right:10px}.headerWrap .row .mobileNav button .ham,.headerWrap .row .mobileNav button .person{align-self:center}.headerWrap .row .mobileNav .mobileNavList{position:absolute;top:100%;right:0;margin-top:10px;background:#fff;padding:10px 0px;color:#000;width:150px;border-radius:10px;display:none}.headerWrap .row .mobileNav .mobileNavList.opener{display:block}.headerWrap .row .mobileNav .mobileNavList ul li{line-height:30px;padding:0 20px}.headerWrap .row .mobileNav .mobileNavList ul li:nth-child(3){border-top:1px solid #ddd;padding-top:10px;margin-top:10px}

.homesecWrap .article2 .row ul{display:flex;flex-wrap:wrap;padding:30px 0}.homesecWrap .article2 .row ul li{width:33.33%;padding:20px}.homesecWrap .article2 .row ul li a{display:flex}.homesecWrap .article2 .row ul li a img{width:40%;margin-right:10px;border-radius:10px}.homesecWrap .article2 .row ul li a div{align-self:center}.homesecWrap .article2 .row .lifeStyle h2{font-size:20px}.homesecWrap .article2 .row .lifeStyle ul{display:flex}.homesecWrap .article2 .row .lifeStyle ul li{width:25%;padding:0 10px}.homesecWrap .article2 .row .lifeStyle ul li img{width:100%;border-radius:10px}.homesecWrap .article3{background:#000;color:#fff;padding:100px 0}.homesecWrap .article3 .row{display:flex}.homesecWrap .article3 .row div{width:52%}.homesecWrap .article3 .row div img{width:100%;padding:10px 10px 0;border-radius:30px 30px 0 0;vertical-align:middle}.homesecWrap .article3 .row div p{background:#222222;margin:0 10px;padding:20px;border-radius:0 0 30px 30px;font-size:14px}.homesecWrap .article3 .row div.right{width:48%;display:flex;flex-direction:column;justify-content:space-between}.homesecWrap .article3 .row div.right div{width:100%}.homesecWrap .article3 .row div.right .rightFst{display:flex;flex-direction:row}

*{margin:0;padding:0;box-sizing:border-box}a{color:inherit;text-decoration:none}ul,ol{list-style:none}.row{max-width:1600px;margin:0 auto;padding:0 80px}

/* font-family: 'Roboto Mono', monospace; */
/* font-family: 'Ubuntu', sans-serif; */
/* font-family: 'Poor Story', cursive; */

* { margin: 0; padding: 0; box-sizing: border-box; outline: none;}
body { font-size: 16px; font-family: 'Ubuntu', sans-serif;}
a { color: inherit; text-decoration: none;}
ul, ol { list-style: none;}
.row { max-width: 1600px; margin: 0 auto; padding: 0 80px;}
.cf:after { content: ''; display: block; clear: both;}
img { width: 100%; vertical-align: middle;} /*img 사이즈는 max-width:100%로*/
button { cursor: pointer; border: none;}
fieldset { display: none;}
legend { display: none;}
h1,h2.h3,h4,h5,h6 { font-size: 100%;}
table { width: 100%; border-collapse: collapse; table-layout: fixed;}
.blind { position: absolute; width: 1px; height: 1px; overflow: hidden; text-indent: -99999px;}
